body {
  margin: 0;
  font-family: 'BubbleBlast', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: rgb(225, 197, 195);
  user-zoom: none;
  user-select: none;
}

#root {
    margin: 1em auto;
    width: 600px;
}

button {
    border-radius: 15px;
    padding: 15px 25px;
    background: #5c59b75a;
    border: none;
    min-width: 250px;
    cursor: pointer;
    color: white;
    font-size: 3rem;
    font-weight: 400;  
    font-family: BunchBlossoms, Arial, Sans Serif;
    border: none;
    min-width: 250px;
    cursor: pointer;
    transform: rotate(0deg);
    transition: all 400ms ease-in;
}

button:hover {
    background: #00102f; 
}

button.spin-right:hover {
    transform: rotate(5deg);
    transition: background 400ms ease-in;
}

button.spin-left:hover {
    transform: rotate(-5deg);
    transition: background 400ms ease-in;
}

body {
	background: #00b4ff;
	color: #333;
	font: 100% BubbleBlast, Arial, Sans Serif;
	height: 100vh;
	margin: 0;
	padding: 0;
	overflow-x: hidden;
}

#background-wrap {
    bottom: 0;
	left: 0;
	position: fixed;
	right: 0;
	top: 0;
	z-index: -1;
}

/* KEYFRAMES */

@-webkit-keyframes animateBubble {
    0% {
        margin-top: 1000px;
    }
    100% {
        margin-top: -100%;
    }
}

@-moz-keyframes animateBubble {
    0% {
        margin-top: 1000px;
    }
    100% {
        margin-top: -100%;
    }
}

@keyframes animateBubble {
    0% {
        margin-top: 1000px;
    }
    100% {
        margin-top: -100%;
    }
}

@-webkit-keyframes sideWays { 
    0% { 
        margin-left:0px;
    }
    100% { 
        margin-left:50px;
    }
}

@-moz-keyframes sideWays { 
    0% { 
        margin-left:0px;
    }
    100% { 
        margin-left:50px;
    }
}

@keyframes sideWays { 
    0% { 
        margin-left:0px;
    }
    100% { 
        margin-left:50px;
    }
}

/* ANIMATIONS */

.x1 {
    -webkit-animation: animateBubble 25s linear infinite, sideWays 2s ease-in-out infinite alternate;
	-moz-animation: animateBubble 25s linear infinite, sideWays 2s ease-in-out infinite alternate;
	animation: animateBubble 25s linear infinite, sideWays 2s ease-in-out infinite alternate;
	
	left: -5%;
	top: 5%;
	
	-webkit-transform: scale(0.6);
	-moz-transform: scale(0.6);
	transform: scale(0.6);
}

